import { RSAA } from "redux-api-middleware";
import { API_ROOT } from "Core/constants";
import {
  GET_TRANSACTION_LIST_REQUEST,
  GET_TRANSACTION_LIST_SUCCESS,
  GET_TRANSACTION_LIST_FAILURE,
  GET_TRANSACTION_DETAILS_REQUEST,
  GET_TRANSACTION_DETAILS_SUCCESS,
  GET_TRANSACTION_DETAILS_FAILURE,
  DOWNLOAD_TRANSACTION_INVOICE_REQUEST,
  DOWNLOAD_TRANSACTION_INVOICE_SUCCESS,
  DOWNLOAD_TRANSACTION_INVOICE_FAILURE,
} from "./constants";

export function getTransactionListAction() {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/transaction/list`,
      method: "GET",
      body: undefined,
      types: [
        GET_TRANSACTION_LIST_REQUEST,
        GET_TRANSACTION_LIST_SUCCESS,
        GET_TRANSACTION_LIST_FAILURE,
      ],
    },
  };
}

export function getTransactionDetailsAction(id) {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/transaction/${id}/view`,
      method: "GET",
      body: undefined,
      types: [
        GET_TRANSACTION_DETAILS_REQUEST,
        GET_TRANSACTION_DETAILS_SUCCESS,
        GET_TRANSACTION_DETAILS_FAILURE,
      ],
    },
  };
}

export function printTransactionInvoice(id) {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/transaction/${id}/invoice/download`,
      method: "GET",
      body: undefined,
      types: [
        DOWNLOAD_TRANSACTION_INVOICE_REQUEST,
        DOWNLOAD_TRANSACTION_INVOICE_SUCCESS,
        DOWNLOAD_TRANSACTION_INVOICE_FAILURE,
      ],
    },
  };
}
