import {
  GET_TRANSACTION_LIST_REQUEST,
  GET_TRANSACTION_LIST_SUCCESS,
  GET_TRANSACTION_LIST_FAILURE,
  GET_TRANSACTION_DETAILS_REQUEST,
  GET_TRANSACTION_DETAILS_SUCCESS,
  GET_TRANSACTION_DETAILS_FAILURE,
  DOWNLOAD_TRANSACTION_INVOICE_REQUEST,
  DOWNLOAD_TRANSACTION_INVOICE_SUCCESS,
  DOWNLOAD_TRANSACTION_INVOICE_FAILURE,
} from "./constants";

const initialState = {
  transactionList: [],
  transaction: {},
};

export default function transactionReducer(
  state = initialState,
  { type, payload }
) {
  switch (type) {
    case GET_TRANSACTION_LIST_REQUEST:
      return {
        ...state,
      };
    case GET_TRANSACTION_LIST_SUCCESS:
      return {
        ...state,
        transactionList: payload.data.transactions,
      };
    case GET_TRANSACTION_LIST_FAILURE:
      return {
        ...state,
      };
    case GET_TRANSACTION_DETAILS_REQUEST:
      return {
        ...state,
      };
    case GET_TRANSACTION_DETAILS_SUCCESS:
      return {
        ...state,
        transaction: payload.data,
      };
    case GET_TRANSACTION_DETAILS_FAILURE:
      return {
        ...state,
      };
    case DOWNLOAD_TRANSACTION_INVOICE_REQUEST:
      return {
        ...state,
      };
    case DOWNLOAD_TRANSACTION_INVOICE_SUCCESS:
      return {
        ...state,
      };
    case DOWNLOAD_TRANSACTION_INVOICE_FAILURE:
      return {
        ...state,
      };
    default:
      return state;
  }
}
